import React, { useEffect, useState } from "react"
import MetaTags from "react-meta-tags"
import { MDBDataTableV5 } from "mdbreact"
import { Row, Col, Card, CardBody, CardTitle, Button } from "reactstrap"
import { connect } from "react-redux"
import { setBreadcrumbItems } from "../../store/actions"
import "./datatables.scss"
import "./billbycss.css"
import axios from "../../axios/axios"
import Modal from "react-modal"
import { doGet } from "../../axios/restutils"
import BillDetailModal from "./billdetailmodal"
const DatatableTables = props => {
  const breadcrumbItems = [
    { title: "SKRKMK", link: "#" },
    { title: "Invoices", link: "#" },
    { title: "List", link: "#" },
  ]
  const [modalshow, setModalshow] = useState(false)
  const toggle = () => {
    setModalshow(!modalshow)
  }

  const ModelContent = () => {
    return (
      <Modal
        isOpen={modalshow}
        onRequestClose={toggle}
        contentLabel="Example Modal"
      >
        <BillDetailModal content={content} />
      </Modal>
    )
  }
  const [content, setContent] = useState({})

  const sumqty = items => {
    let qty = items.map(company => {
      return company.qty
    })
    let sum = 0
    for (let i = 0; i < qty.length; i++) {
      sum += qty[i]
    }
    return sum
  }
  const sumuom = items => {
    let qty = items.map(company => {
      return company.uom
    })
    let sum = 0
    for (let i = 0; i < qty.length; i++) {
      sum += qty[i]
    }
    return sum
  }
  const calcamount = items => {
    let qty = items.map(company => {
      return company.rate * company.qty
    })
    let sum = 0
    for (let i = 0; i < qty.length; i++) {
      sum += qty[i]
    }
    return sum
  }
  const handlebuttonclick = idx => {
    doGet("api/bill-get/" + idx)
      .then(response => {
        window.open("http://skrkmk.in" + response.data.invoice)
      })
      .catch(error => {
        console.log(error)
      })
    console.log(idx)
  }
  const handleeditclick = company => {
    setModalshow(true)
    setContent(company)
    console.log(company)
  }
  const assembleCompany = tableCont => {
    let companies = tableCont.map((company, index) => {
      return {
        id: company.id,
        date: company.date,
        vehicle_no: company.vehicle_no,
        company_name: company.company_name,
        party_name: company.party_name,
        invoice_no: company.invoice_no,
        bill_items: company.bill_items,
        qty: sumqty(company.bill_items),
        uom: sumuom(company.bill_items),
        amount: calcamount(company.bill_items),
        get_bill: (
          <Button
            className="btn btn-info outline"
            onClick={e => {
              handlebuttonclick(company.id)
            }}
          >
            <i className="mdi mdi-cloud-print"></i>
          </Button>
        ),
        edit_bill: (
          <Button
            className="btn btn-dark outline"
            onClick={e => {
              handleeditclick(company)
            }}
          >
            <i className="mdi mdi-grease-pencil"></i>
          </Button>
        ),
      }
    })
    return companies
  }
  useEffect(() => {
    props.setBreadcrumbItems("Company List", breadcrumbItems)
    const getData = async () => {
      const tableCont = await axios.get("api/bill-detail-list/all")
      setData({ ...data, rows: assembleCompany(tableCont.data) })
    }
    getData()
  }, [])

  const [data, setData] = useState({
    columns: [
      {
        label: "Invoice Number",
        field: "invoice_no",
        width: 0,
      },
      {
        label: "Date",
        field: "date",
        width: 0,
      },
      {
        label: "Vehicle No",
        field: "vehicle_no",
        width: 0,
      },
      {
        label: "Company Name",
        field: "company_name",
        width: 0,
      },
      {
        label: "Party Name",
        field: "party_name",
        width: 0,
      },
      {
        label: "UOM",
        field: "uom",
        width: 0,
      },
      {
        label: "Qty",
        field: "qty",
        width: 0,
      },
      {
        label: "Get Bill",
        field: "get_bill",
        width: 0,
      },
      {
        label: "Edit Bill",
        field: "edit_bill",
        width: 0,
      },
    ],
    rows: [],
  })
  return (
    <React.Fragment>
      <MetaTags>
        <title>Company List</title>
      </MetaTags>
      <Row>
        <Col className="col-12">
          <Card>
            <CardBody>
              <CardTitle className="h4">Company List </CardTitle>
              <br></br>
              <MDBDataTableV5
                responsive
                hover
                bordered
                data={data}
                filter="position"
                searchBottom={false}
                searchTop
                barReverse
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
      {modalshow ? <ModelContent /> : null}
    </React.Fragment>
  )
}

export default connect(null, { setBreadcrumbItems })(DatatableTables)
