import React, { useEffect, useState } from "react"
import Select from "react-select"
import axios from "../../axios/axios"
import Switch from "react-switch"
import { Card, CardBody, Col, Row, CardTitle, InputGroup } from "reactstrap"
import Flatpickr from "react-flatpickr"
const GenesGroup = [
  {
    options: [
      { label: "Maize", value: "maize" },
      { label: "Paddy", value: "paddy" },
      { label: "Bajra", value: "bajra" },
      { label: "Wheat", value: "wheat" },
      { label: "Mustard Seeds", value: "mustard_seed" },
    ],
  },
]

const Offsymbol = () => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100%",
        fontSize: 12,
        color: "#fff",
        paddingRight: 2,
      }}
    >
      {" "}
      B
    </div>
  )
}

const OnSymbol = () => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100%",
        fontSize: 12,
        color: "#fff",
        paddingRight: 2,
      }}
    >
      {" "}
      A
    </div>
  )
}
const assembleOption = tableCont => {
  let companies = tableCont.map(company => {
    return {
      label: company.name,
      value: company.id,
    }
  })
  return companies
}

const BillModal = ({ content }) => {
  useEffect(() => {
    const getData = async () => {
      const tableCont = await axios.get("api/bill-to-list/all")
      setBillTo([{ options: assembleOption(tableCont.data) }])
      const tableCon = await axios.get("api/bill-by-list/all")
      setBillBy([{ options: assembleOption(tableCon.data) }])
    }
    getData()
  }, [])
  const [billitem, setbillitem] = useState(content.bill_items)
  const customRow = () => {
    const listItems = billitem.map((cusRow, index) => (
      <div>
        <h5>Bill Item {index + 1}</h5>
        <Row className="mb-4">
          <label
            htmlFor="example-text-input"
            className="col-md-2 col-form-label"
          >
            Genes :
          </label>
          <Select
            className="col-md-3 col-form-control"
            value={{
              label: cusRow.item
                ? cusRow.item.label
                  ? cusRow.item.label
                  : cusRow.item.charAt(0).toUpperCase() + cusRow.item.slice(1)
                : null,
              value: cusRow.item
                ? cusRow.item.value
                  ? cusRow.item.value
                  : cusRow.item
                : null,
            }}
            name="genes"
            isDisabled={true}
            options={GenesGroup}
            classNamePrefix="select2-selection"
          />
          <label
            htmlFor="example-search-input"
            className="col-md-2 col-form-label"
          >
            UOM :
          </label>
          <div className="col-md-3">
            <input
              className="form-control"
              type="number"
              name="uom"
              value={cusRow.uom}
              readOnly={true}
              id="example-number-input"
            />
          </div>
        </Row>
        <Row className="mb-4">
          <label
            htmlFor="example-text-input"
            className="col-md-2 col-form-label"
          >
            Quantity :
          </label>
          <div className="col-md-3">
            <input
              className="form-control"
              type="number"
              name="qty"
              readOnly={true}
              value={cusRow.qty}
              id="example-number-input"
            />
          </div>
          <label
            htmlFor="example-search-input"
            className="col-md-2 col-form-label"
          >
            Rate :
          </label>
          <div className="col-md-3">
            <input
              className="form-control"
              type="number"
              name="rate"
              readOnly={true}
              value={cusRow.rate}
              id="example-number-input"
            />
          </div>
        </Row>
        <Row className="mb-4">
          <label
            htmlFor="example-text-input"
            className="col-md-2 col-form-label"
          >
            PO Number :
          </label>
          <div className="col-md-3">
            <input
              className="form-control"
              type="text"
              name="po_number"
              readOnly={true}
              value={cusRow.po_number}
              id="example-number-input"
            />
          </div>
        </Row>
      </div>
    ))
    return listItems
  }
  const [bilTo, setBillTo] = useState([{ options: [] }])
  const [bilBy, setBillBy] = useState([{ options: [] }])
  const [switch1, setswitch1] = useState(true)
  var parts = content.date.split("-")
  var mydate = new Date(parts[0], parts[1] - 1, parts[2])
  const [dates, setDates] = useState(mydate)
  const [remarks, setRemarks] = useState(content.remarks)
  const [frieght, setfrieght] = useState(content.frieght)
  const [invoiceNo, setInvoiceNo] = useState(content.invoice_no)
  const [selectedbillto, setSelectedbillto] = useState({
    label: content.party_name,
    value: content.bill_to,
  })
  const [selectedbillby, setSelectedbillby] = useState({
    label: content.company_name,
    value: content.bill_by,
  })
  const [vehicleNumber, setVehicleNumber] = useState(content.vehicle_no)
  const handleremarks = e => {
    setRemarks(e.target.value)
  }
  return (
    <Row>
      <Col>
        <Card>
          <CardBody>
            <CardTitle className="h4">
              {content.company_name.toUpperCase() +
                " TO " +
                content.party_name.toUpperCase()}
            </CardTitle>
            <br></br>
            <br></br>
            <Row className="mb-3">
              <div className="col-md-5">
                <Switch
                  uncheckedIcon={<Offsymbol />}
                  checkedIcon={<OnSymbol />}
                  onColor="#626ed4"
                  onChange={() => {
                    setswitch1(!switch1)
                  }}
                  checked={switch1}
                />
              </div>
              <label
                htmlFor="example-text-input"
                className="col-md-2 col-form-label"
              >
                Invoice Number :
              </label>
              <div className="col-md-3">
                <input
                  className="form-control"
                  type="text"
                  value={invoiceNo}
                  readOnly={true}
                  onChange={e => {
                    handleremarks(e)
                  }}
                  id="example-number-input"
                />
              </div>
            </Row>
            <Row className="mb-3">
              <label
                htmlFor="example-email-input"
                className="col-md-2 col-form-label"
              >
                Date :
              </label>
              <div className="col-md-3">
                <InputGroup>
                  <Flatpickr
                    className="col-md-3 form-control"
                    value={dates}
                    options={{
                      enableTime: false,
                      altInput: true,
                      altFormat: "F j, Y",
                      dateFormat: "y-m-d",
                    }}
                    onChange={date => {
                      setDates(date)
                    }}
                  />
                </InputGroup>
              </div>
              <label
                htmlFor="example-email-input"
                className="col-md-2 col-form-label"
              >
                Vehicle Number :
              </label>
              <div className="col-md-3">
                <input
                  className="form-control"
                  type="text"
                  value={vehicleNumber}
                  readOnly={true}
                  id="example-number-input"
                />
              </div>
            </Row>
            <Row className="mb-4">
              <label
                htmlFor="example-text-input"
                className="col-md-2 col-form-label"
              >
                Bill By :
              </label>
              <Select
                className="col-md-3 col-form-control"
                value={selectedbillby}
                isDisabled={true}
                options={bilBy}
                classNamePrefix="select2-selection"
              />
              <label
                htmlFor="example-search-input"
                className="col-md-2 col-form-label"
              >
                Bill To :
              </label>
              <Select
                className="col-md-3 col-form-control"
                value={selectedbillto}
                isDisabled={true}
                options={bilTo}
                classNamePrefix="select2-selection"
              />
            </Row>
            <Row className="mb-3">
              <label
                htmlFor="example-text-input"
                className="col-md-2 col-form-label"
              >
                Remarks :
              </label>
              <div className="col-md-3">
                <input
                  className="form-control"
                  type="text"
                  readOnly={true}
                  value={remarks}
                  id="example-number-input"
                />
              </div>
              <label
                htmlFor="example-email-input"
                className="col-md-2 col-form-label"
              >
                Frieght :
              </label>
              <div className="col-md-3">
                <input
                  className="form-control"
                  type="text"
                  value={frieght}
                  readOnly={true}
                  id="example-number-input"
                />
              </div>
            </Row>
            {customRow()}
          </CardBody>
        </Card>
      </Col>
    </Row>
  )
}

export default BillModal
