import React, { useEffect, useState } from "react"
import MetaTags from "react-meta-tags"
import { MDBDataTableV5 } from "mdbreact"
import { Row, Col, Card, CardBody, CardTitle, Button } from "reactstrap"
import { connect } from "react-redux"
import { setBreadcrumbItems } from "../../store/actions"
import "./datatables.scss"
import Modal from "react-modal"
import "./billbycss.css"
import axios from "../../axios/axios"
import BillToModal from "./billtomodal"
const DatatableTables = props => {
  const breadcrumbItems = [
    { title: "SKRKMK", link: "#" },
    { title: "COMPANY", link: "#" },
    { title: "Company List", link: "#" },
  ]
  const [modalshow, setModalshow] = useState(false)
  const toggle = () => {
    setModalshow(!modalshow)
  }

  const customStyles = {
    content: {
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
  }

  const ModelContent = () => {
    return (
      <Modal
        isOpen={modalshow}
        onRequestClose={toggle}
        contentLabel="Example Modal"
      >
        <BillToModal content={content} />
      </Modal>
    )
  }
  const [content, setContent] = useState({})
  const handleRowClick = props => {
    console.log(props)
    setModalshow(true)
    setContent(props)
  }
  const assembleCompany = tableCont => {
    let companies = tableCont.map(company => {
      return {
        name: company.name,
        gstin: company.gstin,
        expense: company.expense,
        address: company.address,
        state: company.state,
        state_code: company.state_code,
        bill_type: company.bill_type == "MandiIn" ? "Yes" : "No",
        id: company.id,
        clickEvent: e => {
          handleRowClick(e)
        },
      }
    })
    return companies
  }
  useEffect(() => {
    props.setBreadcrumbItems("Company List", breadcrumbItems)
    const getData = async () => {
      const tableCont = await axios.get("api/bill-to-list/all")
      setData({ ...data, rows: assembleCompany(tableCont.data) })
    }
    getData()
  }, [])

  const [data, setData] = useState({
    columns: [
      {
        label: "Name",
        field: "name",
        width: 150,
      },
      {
        label: "GSTIN",
        field: "gstin",
        width: 270,
      },
      {
        label: "Mandi In",
        field: "bill_type",
        width: 200,
      },
      {
        label: "Address",
        field: "address",
        width: 150,
      },
      {
        label: "State",
        field: "state",
        width: 100,
      },
    ],
    rows: [],
  })
  return (
    <React.Fragment>
      <MetaTags>
        <title>Company List</title>
      </MetaTags>
      <Row>
        <Col className="col-12">
          <Card>
            <CardBody>
              <CardTitle className="h4">Company List </CardTitle>
              <br></br>
              <MDBDataTableV5
                responsive
                hover
                bordered
                data={data}
                filter="position"
                searchBottom={false}
                barReverse
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
      {modalshow ? <ModelContent /> : null}
    </React.Fragment>
  )
}

export default connect(null, { setBreadcrumbItems })(DatatableTables)
