import React, { useEffect, useState } from "react"
import Select from "react-select"
import { Card, CardBody, Col, Row, CardTitle } from "reactstrap"
import axios from "../../axios/axios"
import Switch from "react-switch"
import Expense from "./expense"
const Offsymbol = () => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100%",
        fontSize: 12,
        color: "#fff",
        paddingRight: 2,
      }}
    >
      {" "}
      No
    </div>
  )
}

const OnSymbol = () => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100%",
        fontSize: 12,
        color: "#fff",
        paddingRight: 2,
      }}
    >
      {" "}
      Yes
    </div>
  )
}
const StateGroup = [
  {
    options: [
      { label: "Andhra Pradesh", value: 37 },
      { label: "Arunachal Pradesh", value: 12 },
      { label: "Assam", value: 18 },
      { label: "Bihar", value: 10 },
      { label: "Chattisgarh", value: 22 },
      { label: "Delhi", value: 7 },
      { label: "Goa", value: 30 },
      { label: "Gujarat", value: 24 },
      { label: "Haryana", value: 6 },
      { label: "Himachal Pradesh", value: 2 },
      { label: "Jammu and Kashmir", value: 1 },
      { label: "Jharkhand", value: 20 },
      { label: "Karnataka", value: 29 },
      { label: "Kerala", value: 32 },
      { label: "Lakshadweep Islands", value: 31 },
      { label: "Madhya Pradesh", value: 23 },
      { label: "Maharashtra", value: 27 },
      { label: "Manipur", value: 14 },
      { label: "Meghalaya", value: 17 },
      { label: "Mizoram", value: 15 },
      { label: "Nagaland", value: 13 },
      { label: "Odisha", value: 21 },
      { label: "Pondicherry", value: 34 },
      { label: "Punjab", value: 3 },
      { label: "Rajasthan", value: 8 },
      { label: "Sikkim", value: 11 },
      { label: "Tamil Nadu", value: 33 },
      { label: "Telangana", value: 36 },
      { label: "Tripura", value: 16 },
      { label: "Uttar Pradesh", value: 9 },
      { label: "Uttarakhand", value: 5 },
      { label: "West Bengal", value: 19 },
    ],
  },
]

const BillModal = ({ content }) => {
  const [editable, seteditable] = useState(false)
  const [switch1, setswitch1] = useState(
    content.bill_type == "Yes" ? true : false
  )

  const [selectedGroup, setselectedGroup] = useState({
    label: content.state,
    value: content.state_code,
  })
  function handleSelectGroup(selectedGroup) {
    console.log(selectedGroup)
    setselectedGroup(selectedGroup)
  }
  const deleteclick = () => {
    axios.delete("api/bill-to/" + content.id).then(response => {
      console.log(response)
    })
  }
  const handleSubmit = () => {
    axios.patch("api/bill-by/" + content.id, formData).then(response => {
      console.log(response)
    })
  }
  const [formExpense, setFormExpense] = useState({
    tulai: content.expense ? content.expense.tulai : 0,
    dharmada: content.expense ? content.expense.dharmada : 0,
    loading_charges: content.expense ? content.expense.loading_charges : 0,
    wages: content.expense ? content.expense.wages : 0,
    mandi_shulk: content.expense ? content.expense.mandi_shulk : 0,
    sutli: content.expense ? content.expense.sutli : 0,
    commision: content.expense ? content.expense.commision : 0,
    vikas_shulk: content.expense ? content.expense.vikas_shulk : 0,
    bardana: content.expense ? content.expense.bardana : 0,
    others: content.expense ? content.expense.others : 0,
  })
  const [formData, setFormData] = useState({
    name: content.name,
    address: content.address,
    gstin: content.gstin,
    bill_type: "MandiIn",
    state: content.state,
    state_code: content.state_code,
  })
  const handleChange = e => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }
  const { name, address, gstin } = formData
  return (
    <Row>
      <Col>
        <Card>
          <CardBody>
            <CardTitle className="h4">{content.name}</CardTitle>
            <br></br>
            <Row className="mb-3">
              <label
                htmlFor="example-email-input"
                className="col-md-2 col-form-label"
              >
                Name :
              </label>
              <div className="col-md-3">
                <input
                  className="form-control"
                  type="text"
                  name="name"
                  value={name}
                  readOnly={!editable}
                  onChange={e => handleChange(e)}
                  id="example-number-input"
                />
              </div>
              <label
                htmlFor="example-search-input"
                className="col-md-2 col-form-label"
              >
                GSTIN :
              </label>
              <div className="col-md-3">
                <input
                  className="form-control"
                  type="text"
                  readOnly={!editable}
                  name="gstin"
                  value={gstin}
                  onChange={e => handleChange(e)}
                  id="example-number-input"
                />
              </div>
            </Row>
            <Row className="mb-4">
              <label
                htmlFor="example-email-input"
                className="col-md-2 col-form-label"
              >
                Address :
              </label>
              <div className="col-md-3">
                <input
                  readOnly={!editable}
                  className="form-control"
                  type="text"
                  name="address"
                  value={address}
                  onChange={e => handleChange(e)}
                  id="example-number-input"
                />
              </div>
              <label
                htmlFor="example-text-input"
                className="col-md-2 col-form-label"
              >
                State :
              </label>
              <Select
                className="col-md-3 col-form-control"
                value={selectedGroup}
                isDisabled={!editable}
                onChange={e => {
                  handleSelectGroup(e)
                }}
                options={StateGroup}
                classNamePrefix="select2-selection"
              />
            </Row>
            <Row className="mb-4">
              <div>
                <label
                  htmlFor="example-text-input"
                  className="col-md-2 col-form-label"
                >
                  Mandi In
                </label>
                <Switch
                  uncheckedIcon={<Offsymbol />}
                  checkedIcon={<OnSymbol />}
                  onColor="#626ed4"
                  disabled={!editable}
                  onChange={() => {
                    setswitch1(!switch1)
                    setFormData({
                      ...formData,
                      bill_type: switch1 ? "MandiIn" : "MandiOut",
                    })
                  }}
                  checked={switch1}
                />
              </div>
            </Row>
            {switch1 && (
              <Expense
                formExpense={formExpense}
                setFormExpense={setFormExpense}
                editable={editable}
              />
            )}
            <Row className="mb-3">
              <div className="col-1 text-end">
                <button
                  type="submit"
                  className="btn btn-primary"
                  onClick={() => {
                    deleteclick()
                  }}
                >
                  DELETE
                </button>
              </div>
              <div className="col-9 text-end">
                {!editable ? (
                  <button
                    className="btn btn-primary w-md waves-effect waves-light"
                    type="submit"
                    onClick={() => {
                      seteditable(true)
                    }}
                  >
                    EDIT
                  </button>
                ) : (
                  <button
                    className="btn btn-primary w-md waves-effect waves-light"
                    type="submit"
                    onClick={() => {
                      handleSubmit()
                    }}
                  >
                    SUBMIT
                  </button>
                )}
              </div>
            </Row>
          </CardBody>
        </Card>
      </Col>
    </Row>
  )
}

export default BillModal
