import React, { useEffect, useState } from "react"
import MetaTags from "react-meta-tags"
import { MDBDataTableV5 } from "mdbreact"
import { Row, Col, Card, CardBody, CardTitle, Button } from "reactstrap"
import { connect } from "react-redux"
import { setBreadcrumbItems } from "../../store/actions"
import "./datatables.scss"
import Modal from "react-modal"
import "./billbycss.css"
import { doGet } from "../../axios/restutils"
import BillModal from "./billmodal"
const DatatableTables = props => {
  const breadcrumbItems = [
    { title: "SKRKMK", link: "#" },
    { title: "COMPANY", link: "#" },
    { title: "Company List", link: "#" },
  ]
  const [modalshow, setModalshow] = useState(false)
  const toggle = () => {
    setModalshow(!modalshow)
  }

  const ModelContent = () => {
    return (
      <Modal
        isOpen={modalshow}
        onRequestClose={toggle}
        contentLabel="Example Modal"
      >
        <BillModal content={content} />
      </Modal>
    )
  }
  const [content, setContent] = useState({})
  const handleRowClick = props => {
    setModalshow(true)
    setContent(props)
  }
  const assembleCompany = tableCont => {
    let companies = tableCont.map(company => {
      return {
        name: company.name,
        gstin: company.gstin,
        mobile1: company.mobile1,
        mobile2: company.mobile2,
        address: company.address,
        bank_account_no: company.bank_account_no,
        state: company.state,
        state_code: company.state_code,
        bank_name: company.bank_name,
        bank_branch: company.bank_branch,
        bank_ifsc: company.bank_ifsc,
        id: company.id,
        clickEvent: e => {
          handleRowClick(e)
        },
      }
    })
    return companies
  }
  useEffect(() => {
    props.setBreadcrumbItems("Company List", breadcrumbItems)
    const getData = async () => {
      const tableCont = await doGet("api/bill-by-list/all")
      setData({ ...data, rows: assembleCompany(tableCont.data) })
    }
    getData()
  }, [])

  const [data, setData] = useState({
    columns: [
      {
        label: "Name",
        field: "name",
        width: 150,
      },
      {
        label: "GSTIN",
        field: "gstin",
        width: 270,
      },
      {
        label: "Mobile No.",
        field: "mobile1",
        width: 200,
      },
      {
        label: "Alternative Mobile No.",
        field: "mobile2",
        width: 100,
      },
      {
        label: "Address",
        field: "address",
        width: 150,
      },
    ],
    rows: [],
  })
  return (
    <React.Fragment>
      <MetaTags>
        <title>Company List</title>
      </MetaTags>
      <Row>
        <Col className="col-12">
          <Card>
            <CardBody>
              <CardTitle className="h4">Company List </CardTitle>
              <br></br>
              <MDBDataTableV5
                responsive
                hover
                bordered
                data={data}
                filter="position"
                searchBottom={false}
                barReverse
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
      {modalshow ? <ModelContent /> : null}
    </React.Fragment>
  )
}

export default connect(null, { setBreadcrumbItems })(DatatableTables)
